html,body{
  background-color: black;
  color:#ffff;
  -ms-overflow-style: none; 
  scrollbar-width: none; 
}

.fs{
 -webkit-font-smoothing: antialiased;
  font-family:Monument Extended,sans-serif;
  font-size: 125px;
  letter-spacing: 20px;
  margin: 0;
  font-weight: bold;
  text-align: center;
}
.textStroks-true {
   color: transparent;
   -webkit-text-stroke: 1px white;
   transition: transform 0.8s ease-in-out;
   text-shadow: none;
}
.textStroks-false {
   color: white;
   transition: transform 0.8s ease-in-out;
   -webkit-text-stroke: 1px white;
   text-shadow: none;
}
.mainDiv{
 position: fixed;
 z-index: 10;
}
